
import { defineComponent } from "vue";

export default defineComponent({
  name: "App",
  components: {},
  data: () => {
    return {
      now: "00:00:00",
      time1: 0 as number,
      count: 5 as number,
      timer: 0 as number,
      audio: {} as HTMLAudioElement,
      started: false as boolean,
    };
  },

  mounted: function () {
    this.time();
    setInterval(this.time, 1000);
    this.audio = new Audio("./alarm.mp3");
  },

  methods: {
    time: function () {
      let date = new Date();
      this.now = date.toLocaleTimeString();
    },

    start: function () {
      if (this.count > 0 && this.started === false) {
        this.timer = setInterval(this.countDown, 1000);
        this.started = true;
      }
    },

    stop: function () {
      clearInterval(this.timer);
      this.audio.pause();
      this.started = false;
      console.log("Stop");
    },

    countDown: function () {
      this.count--;
      if (this.count === 0) {
        clearInterval(this.timer);
        this.audio.play();
        this.started = false;
      }
    },
  },
});
